<template>
   <div class="ReferenceData">
      <div class="dataTop clearfix">
        <span>当前位置: 首页 >
          <i>参考资料</i>
        </span>
        <div class="upload">
            <button @click="show">上传资料</button>
        </div>
        <div class="model" v-show="isShow">
          <div class="box">
            <div class="boxHeader">
                <b>上传资料</b>
                <i class="el-icon-close" @click="boxHide"></i>
            </div>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="选择类型" prop="region">
                  <el-select v-model="ruleForm.region" placeholder="请选择类型"  >
                    <el-option
                      v-for="(item,index) in lists" 
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="输入内容" prop="desc">
                  <el-input type="textarea" v-model="ruleForm.desc"></el-input>
                </el-form-item>
                <el-upload 
                  ref="upload"
                  class="upload-demo"
                  :action="$store.state.protocol+$store.state.host+$store.state.pdf_path"
                  :on-change="handleChange"
                  :data="uploadFile"
                  :auto-upload="false"
                  :on-remove="handleRemove"
                  :on-exceed="handleExceed"
                  :limit="1" accept="application/pdf"
                  :file-list="fileList3">
                  <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                  <!-- <div slot="tip" class="el-upload__tip">只能上传一张一个pdf文件</div> -->
                  <br>
                  <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload('ruleForm')" class="btnUpload">上传文件</el-button>
                </el-upload>
            </el-form>
          </div>
        </div> 
      </div>
      <div class="dataList" v-for="(item,index) in lists" :key="index">
        <div class="dataListTop clearfix">
          <div class="dataListTopL">
            <span>{{item.name}}</span>
            <b>|</b>
            <i>{{item.Market}}</i>
          </div>
          <div class="dataListTopR">
            <span>更多</span> <i class="el-icon-arrow-right"></i>
          </div>
        </div>
        <div class="dataListTopB">
          <ul>
            <li v-for="(data,index) in item.database " :key="index" @click="watch(data.id)">
                  <!-- <span>{{ data.title }}</span> -->
                  <span><router-link :to="'/WatchPdf/' + data.id">{{ data.description }}</router-link></span>
                  <b @click="deltPer(data.id)">删除</b>               
            </li>
          </ul>
        </div>
      </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pdf_Name:"",//文件名
      // 上传文件参数
      uploadFile: {
        type_id: "",
        description: ""
      },
      fileList3: [],
      ruleForm: {
        region: "",
        desc: "",
        file: []
      },
      // 非空提示信息
      rules: {
        region: [{ required: true, message: "请选择类型", trigger: "change" }],
        desc: [{ required: true, message: "请输入内容", trigger: "blur" }]
      },
      lists: [],
      arr: [],
      isShow: false,
      value: "请选择"
    };
  },
  methods: {
    //删除资料信息
    deltPer(valId) {
      let _this = this;
      this.$confirm("此操作将删除该资料, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          _this.$axios
            .post("/api/database-del", {
              id: valId
            })
            .then(function(res) {
              if (res.data.message === "删除成功") {
                _this.$message({
                  message: "删除成功",
                  type: "success"
                });
               _this.getData()
              } else {
                _this.$message.error("删除失败");
              }
            })
            .catch(function(error) {
              console.log(error);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 点击查看pdf
    watch(val) {
    },
    // 文件暂存
    handleRemove() {
      this.ruleForm.file = [];
    },
    // 上传文件
    submitUpload(formName) {
      if (this.ruleForm.file.length == 0) {
        this.$message({
          message: "请先选则pdf文件",
          type: "warning"
        });
      } else {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.uploadFile.type_id = this.ruleForm.region;
            this.uploadFile.description = this.ruleForm.desc;
            this.$refs.upload.submit();
            this.isShow = !this.isShow;
//            window.location.reload();
          } else {
            return false;
          }
        });
      }
    },
    handleChange(file, fileList) {
      this.ruleForm.file = file;
    },
    // 重置信息
    // resetForm(formName) {
    //   this.$refs[formName].resetFields();
    // },
    // 上传文件条件
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制只能选择 1 个文件`);
    },
    // 获取资料细信息
    getData() {
      let _this = this;
      _this.$axios
        .post("/api/database-list", {})
        .then(function(res) {
          _this.lists = res.data.data.data;
          for(var i in res.data.data.data){
            for(var v in res.data.data.data[i]){
              for(var o in res.data.data.data[i][v]){

              }
            }
          }
          _this.pdf_Name = res.data.data.data[i][v][o].original_file
//          console.log(222)
//          console.log(res.data.data.data[i][v][o].id)
//          console.log(_this.pdf_Name)
//          console.log(_this.lists)
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 模态框
    show() {
      this.isShow = !this.isShow;
    },
    boxHide() {
      this.isShow = !this.isShow;
      this.ruleForm.region = "";
      this.ruleForm.desc = "";
    }
  },
  created() {
    this.$router.push("/SuperReference");
    console.log(1111)
    console.log(this.$store.state.host);
    console.log(this.$store.state.protocol);
  },
  mounted() {
    this.getData();
  }
};
</script>
<style lang="less" rel="stylesheet/less">
@import "../../../static/css/base.css";
body{
  padding: 0 !important ;
}
.ReferenceData {
  margin: 0 auto;
  width: 1280px;
  .dataTop {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background-color: #f1f1f1;
    margin-bottom: 15px;
    span {
      // float: left;
      margin-left: 80px;
      color: #4a4a4a;
      i {
        color: #4a90e2;
        cursor: pointer;
        margin-left: 3px;
      }
    }
    .upload {
      float: right;
      button {
        width: 137.3px;
        height: 30px;
        border-radius: 3px;
        background-color: #4a90e2;
        font-family: PingFangSC;
        font-size: 16px;
        text-align: center;
        color: #ffffff;
        cursor: pointer;
        outline: none;
        margin-right: 20px;
      }
    }
    .model {
      position: fixed;
      z-index: 99;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.699);
      display: table;
      .box {
        background-color: white;
        width: 537px;
        height: 440px;
        margin: 0 auto;
        position: relative;
        top: 180px;
        .boxHeader {
          height: 50px;
          background-color: #4a90e2;
          text-align: center;
          b {
            line-height: 50px;
            font-size: 22px;
            letter-spacing: -0.3px;
            color: #ffffff;
          }
          i {
            color: white;
            font-size: 25px;
            font-weight: 500;
            position: relative;
            left: 170px;
            top: 3px;
            cursor: pointer;
          }
        }
        .el-form {
          position: relative;
          top: 50px;
          left: 40px;
        }
        .el-textarea {
          width: 280px;
        }
        .el-button {
          background-color: #409eff;
          span {
            margin: 0;
            color: white;
          }
        }
        .el-upload {
          position: relative;
          left: 100px;
          top: 10px;
        }
        .el-textarea {
          max-height: 140px;
        }
        .el-upload-list {
          margin: 0;
          padding: 0;
          list-style: none;
          width: 400px;
          margin-left: 30px;
          margin-top: -30px;
          position: relative;
          top: -8px;
          height: 20px;
        }
        .el-upload-list--text {
          display: inline-block;
          position: relative;
          top: -25px;
        }
        .el-upload-list__item .is-ready {
          margin: 0;
        }
        .el-button--default {
          position: relative;
          left: 130px;
        }
        .btnUpload {
          margin-left: 10px;
          position: relative;
          top: 46px;
          border: none;
          left: 105px;
          z-index: 999;
          height: 38px;
          width: 231px;
        }
        .el-input__inner {
          width: 280px;
        }
      }
    }
  }
  .dataList:nth-child(2) {
    margin-bottom: 0;
  }
  .dataList {
    float: left;
    margin: 0 55px 25px 55px;
    .dataListTop {
      width: 516px;
      border-bottom: 1px solid #c7c7c7;
      margin: 0 0 19.5px 0;
      .dataListTopL {
        float: left;
        span {
          font-family: PingFangSC;
          font-size: 20px;
          font-weight: 500;
          text-align: left;
          color: #4a90e2;
          border-bottom: 3px solid #4a90e2;
        }
        b {
          display: inline-block;
          width: 3px;
          height: 21px;
          i {
            height: 14px;
          }
        }
        i {
          font-family: PingFangSC;
          font-size: 16px;
          font-weight: 500;
          text-align: left;
          color: #abaaab;
        }
      }
      .dataListTopR {
        float: right;
        span {
          line-height: 31px;
          font-family: PingFangSC;
          font-size: 16px;
          text-align: right;
          color: #abaaab;
        }
      }
    }
    .dataListTopB {
      height: 300px;
      overflow: hidden;
      ul {
        margin-left: 20px;
        li {
          list-style-type: square;
          color: red;
          font-size: 14px;
          cursor: pointer;
          line-height: 1.6;
          i {
            font-family: PingFangSC;
            font-size: 16px;
            line-height: 1.88;
            text-align: left;
            color: #4a4a4a;
          }
          b {
            color: #4a90e2;
            font-size: 14px;
            float: right;
            margin-left: 2px;
          }
        }
        span {
          font-family: PingFangSC;
          font-size: 16px;
          font-weight: 500;
          text-align: left;
          color: #4a4a4a;
        }
      }
    }
  }
}
</style>




